import React from 'react';

const Skillcard = ({skill:{icon,title,about}}) =>
    <div className="col-md-6">
        <div className="skill_card">
            <img src={icon} alt="" className="skill_icon"/>
            <div className="skill_body">
                <h5 className="skill_title">{title}</h5>
                <p className="skill_about">{about}</p>
            </div>
        </div>
    </div>

 
export default Skillcard;