import React, {useState,useEffect} from 'react';
import {Link} from 'react-router-dom'

function Navbar() {
    const [active, setActive] = useState('')
    useEffect(() => {
        let currentURL = window.location.href;
        console.log(currentURL);
        if(currentURL.endsWith('/Resume'))
            setActive('Resume')
        else if(currentURL.endsWith('/Portfolio'))
            setActive('Portfolio')
        else if(currentURL.endsWith('/resume/'))
            setActive('About')

    }, [active]);
    return (
        <div>
            <div className="navbar">
                <div className="active">
                    {active}
                </div>
                <div className="items">
                    { active !== 'About' &&
                        <Link to = '/'>
                            <div className="navitem" onClick={() => setActive('About')}>About</div>
                        </Link>
                    }
                    { active !=='Resume' &&
                        <Link to = '/Resume'>
                            <div className="navitem" onClick={() => setActive('Resume')}>Resume</div>
                        </Link>
                    }
                    { active !=='Portfolio' &&
                        <Link to = '/Portfolio'>
                            <div className="navitem" onClick={() => setActive('Portfolio')}>Portfolio</div>
                        </Link>
                    }
                </div>
            </div>
        </div>
    );
}

export default Navbar;