import React from 'react';

const Bar = ({prop:{icon,name,level}}) => {
    const bar_width = `${level}%`
    return(
        <div className="bar">
            <div className="bar_wrapper bar_wrapper_color" style={{
                'width': bar_width
                
                }}>
                    {console.log(bar_width)}
                <span className="bar_name">
                    <img src={icon} alt="" className="bar_icon mr-2"/>{name}
                </span>
                
            </div>
        </div>    
    );
}

export default Bar;
