import React from 'react';
import github from '../assets/icons/github.svg'
import pin from '../assets/icons/pin.svg'
import tie from '../assets/icons/tie.svg'
import mightycoder from '../assets/image_illustration.svg'
import resume from '../assets/resume.pdf'

const Sidebar = () => {
    return(
        <div className="sidebar">
            <img src={mightycoder} alt="" className="sidebar_image"/>
            <div className="sidebar_name">Tarun <span>Sharma</span></div>
            <div className="sidebar_item ">Full-Stack Developer</div>
            <div className="sidebar_item sidebar_location"><img src={pin} alt="" className="sidebar_icon"/> Gurugram,India</div>
            <a href="https://tarundev.com" className="resume_btn">
                <div className="sidebar_item sidebar_title">
                   <a href={resume} target="_blank">
                   <img src={tie} alt="" className="sidebar_icon"/>Download Resume
                   </a>
                </div>
            </a>
            {/* <figure className="sidebar_social-icons">
                <a href="https://tarundev.com"><img src={facebook} alt="" className="sidebar_icon"/></a>
                <a href="https://tarundev.com"><img src={github} alt="" className="sidebar_icon"/></a>
            </figure> */}
            <div className="contact">
                <div className="sidebar_item sidebar_title" target="_blank">
                    <a href="https://github.com/TarunWebdev">
                        <img src={github} alt="" className="sidebar_icon"/> Github
                    </a>
                </div>
                
                <div className="sidebar_item sidebar_title"><a href="https://tarundev.com" target="_blank">Portfolio Website</a></div>
            </div>
            <div className="sidebar_item sidebar_email">
                <a href="mailto:shatmatarun1019@gmail.com" target="_blank" rel="noopener noreferrer">Email ME</a>
            </div>
        </div>
    );
}

export default Sidebar;
