import React , {useState} from 'react';
import data_projects from './data/projects_data'
import ProjectCard from './ProjectCard';

function Portfolio(props) {

    const [projects, setProjects] = useState(data_projects);

    const handleFilterCategory = (name) =>{
        const new_arr = data_projects.filter(project => project.category.includes(name))
        setProjects(new_arr)
    }
    return (
       <div className="container projects">
           <div className="projects_navbar">
               <div onClick={()=>setProjects(data_projects)}>All</div>
               <div onClick={()=>handleFilterCategory('django')}>Django</div>
               <div onClick={()=>handleFilterCategory('wordpress')}>WordPress</div>
               <div onClick={()=>handleFilterCategory('htmlcss','bootstrap')}>Html/Css</div>
               <div onClick={()=>handleFilterCategory('nodejs')}>Node.js</div>
           </div>
            <div className="row">
                {
                    projects.map(project =>
                        <ProjectCard key={project.name} project={project} />
                        )
                }
            </div>
       </div>
    );
}

export default Portfolio;