import React from 'react';
import api from '../assets/icons/api.svg'
import algo from '../assets/icons/algo.svg'
import backend from '../assets/icons/backend.svg'
import ux from '../assets/icons/ux.svg'
import repair from '../assets/icons/repair.svg'
import puzzle from '../assets/icons/puzzle.svg'
import Skillcard from './Skillcard';

const skills = [
    {
        icon: api,
        title:"Frontend Development",
        about:"I can build a beautiful and scalable SPA using Bootstrap, Django and React.js"
    },
    {
        icon: backend,
        title:"Backend Development",
        about:"handles database and complex functionality using django and firebase"
    },
    {
        icon: ux,
        title:"UX/UI Designing",
         about:"minimalistic user interface designer using figma"
    },
    {
        icon: algo,
        title:"Algo and DS",
        about:"a daily problem solver in HackerRank and an expert data structure"
    },
    {
        icon: repair,
        title:"Python Developer",
        about:"developer of efficient, low-latency and performant applications using python"
    },
    {
        icon: puzzle,
        title:"Graphics Designing",
        about:"a hobbiest of designing logos and digital art using photoshop and illustrator"
    }
]

function About(props) {
    return (
        <div className="about">
            <h5 className="intro">
            A Tech Enthusiast and Full-Stack Web Developer, interested in UX/UI Development and Creating End to End Projects.
            </h5>
            <div className="container">
                <h6 className="heading">What I offer..</h6>
                <div className="row">
                    {
                        skills.map(skills =>                          
                            <Skillcard skill={skills}/>
                            )
                    }
                </div>
            </div>
        </div>
    );
}

export default About;